@charset "utf-8";

.input_item {
  margin: 50px auto;
  width: 100%;
}

.input_item p {
  padding: 10px 0px;
}

.input_item p input {
  display: block;
  width: 100%;
  height: 50px;
}

.input_item label {
  font-weight: 600;
  font-size: 1rem;
}

.input_item p input[type=button] {
  width: 100%;
  border: none;
  border-radius: 3px;
  background: rgb(64, 180, 80);
  color: #fff;
  font-weight: 600;
  cursor: pointer;
}

.product_list {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
  text-align: center;
}

.product_list li img {
  width: 80%;
}

.product_list li p:nth-child(3) {
  font-size: 1rem;
  font-weight: 600;
}

.product_list li p:nth-child(4) input {
  width: 80%;
  /* display: block; */
  border: none;
  border-radius: 3px;
  text-align: center;
  padding: 10px 0px;
  margin-bottom: 2px;
  ;
  color: #fff;
  background: #3ca73c;
  cursor: pointer;
}

.product_list li p:last-child input {
  width: 80%;
  /* display: block; */
  border: none;
  border-radius: 3px;
  text-align: center;
  padding: 10px 0px;
  color: #fff;
  background: #333;
  cursor: pointer;
}