@charset "utf-8";

/* 공통서식 header,footer */
header {
  line-height: 60px;
  text-align: center;
  position: fixed;
  /* height: 80px; */
  background: #fff;
  z-index: 100;
  width: 100%;
  border-bottom: 1px solid #ddd;
}

header h1 img {
  width: 140px;
}

main {
  padding: 15px 10px;
}

footer {
  clear: both;
  background: var(--base_color);
  color: #fff;
  text-align: center;
  line-height: 100px;
  position: relative;
}

footer>address {
  font-style: normal;
}

footer button {
  width: 35px;
  height: 35px;
  position: absolute;
  display: inline;
  top: 25%;
  right: 10%;
  background: #fff;
  border-radius: 50%;

}